body {
  overflow: hidden;
  --color1: #b0efe9;
  --color2: #feced8;
  --color3: #c7f4d7;
  --color4: #bfe0f4;
  --color5: #c5c8f4;
  --color6: #efc5f4;
  --color7: #99cdee;
  --color8: #db75e64f;
  --header-color: var(--color4);
  --chip-color: var(--color3);
}

.ribbon {
  font-size: 12px;
  font-weight: bold;
  font-family: "Roboto";
}
.ribbon {
  --f: 0.5em;
  --r: 0.8em;

  position: absolute;
  right: calc(-1 * var(--f));
  padding-inline: 0.25em;
  line-height: 1.5;
  background: var(--color2);
  border-bottom: var(--f) solid #0005;
  border-left: var(--r) solid #0000;
  clip-path: polygon(
    var(--r) 0,
    100% 0,
    100% calc(100% - var(--f)),
    calc(100% - var(--f)) 100%,
    calc(100% - var(--f)) calc(100% - var(--f)),
    var(--r) calc(100% - var(--f)),
    0 calc(50% - var(--f) / 2)
  );
}

.ribbon-bottom {
  bottom: 0px;
}

.ribbon-top {
  top: 10px;
}

.ribbon-in-padding {
  padding-inline: 1.25em;
}

.scroll-custom::-webkit-scrollbar {
  width: 0.6em;
}

.scroll-custom::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.3);
}

.scroll-custom::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
